* {
    box-sizing: border-box;
}

html, body {
    margin: 0;
    padding: 0;
    font-family: 'Signika', sans-serif;
}

.boxMessage {
   display: flex;
   flex-direction: column; 
   justify-content: space-around;
   max-width: 40%;
   min-height: 50vh;
   align-items: center;
   margin: 0 auto;
   padding: 5em;
}

textarea {
    min-width: 50%;
    min-height: 20vh;
    margin-bottom: 1em;
    background-color: rgb(190, 217, 243);
    border-radius: 5px;
}

button {
    border-radius: 5px;
    border: none;
    background-color: slateblue;
    padding: 0.5em 1em;
    color: #fff;
}

button:hover,
button:focus {
    cursor: pointer;
    transform: scale(1.2);
}

